/* Roboto - Thin 100 */
@font-face {
  font-family: 'Roboto';
  src: url('../css/fonts/Roboto-Thin.woff2') format('woff2'),
  url('../css/fonts/Roboto-Thin.woff') format('woff');
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: url('../css/fonts/Roboto-ThinItalic.woff2') format('woff2'),
  url('../css/fonts/Roboto-ThinItalic.woff') format('woff');
  font-weight: 100;
  font-style: italic;
  font-display: swap;
}

